"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";

export default function Header() {
  const [user, setUser] = useState(null);
  const [points, setPoints] = useState(0);
  const router = useRouter();

  const fetchUser = async () => {
    const res = await fetch("/api/auth/me");
    const data = await res.json();

    if (data.user) {
      setUser(data.user);
      const actionsRes = await fetch("/api/actions");
      const actionsData = await actionsRes.json();
      const totalPoints = actionsData.reduce(
        (sum, a) => sum + (data.user.experienceLevel === "beginner" ? 20 : 10),
        0
      );
      setPoints(totalPoints);
    } else {
      setUser(null);
      setPoints(0);
    }
  };

  useEffect(() => {
    fetchUser();
    const listener = () => fetchUser();
    window.addEventListener("userUpdated", listener);
    return () => window.removeEventListener("userUpdated", listener);
  }, []);

  const handleLogout = async () => {
    await fetch("/api/auth/logout", { method: "POST" });
    window.dispatchEvent(new Event("userUpdated"));
    router.push("/profile");
  };

  return (
    <nav className="navbar">
      <div className="logo">
        <span>ðŸŒ¿</span> EcoTrack
      </div>

      <div className="nav-links">
        <a href="/">Home</a>

        {user && (
          <>
            <a href="/dashboard">Dashboard</a>
            <a href="/actions">Log Actions</a>
            <a href="/goals">Goals</a>
            <a href="/recommendations">Recommendations</a>
            <span className="user-info">
              {user.experienceLevel === "beginner" ? "ðŸŒ± Beginner" : "ðŸŒ³ Experienced"}
              <span className="points-badge">{points} pts</span>
            </span>
          </>
        )}

        <a href="/profile">{user ? "Profile" : "Login/Register"}</a>
        {user && <button onClick={handleLogout}>Logout</button>}
      </div>
    </nav>
  );
}
